body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
}

.search-form {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
    flex-wrap: wrap;
}

.search-form input[type="text"] {
    font-size: 16px;
    padding: 8px;
    margin-right: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    width: 300px;
    max-width: 100%;
}

.search-form select {
    font-size: 16px;
    padding: 8px;
    margin-right: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    max-width: 100%;
}

button {
    margin: 15px;
    font-size: 16px;
    padding: 8px 16px;
    border-radius: 5px;
    border: none;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
}

.search-results {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
}

a {
    text-decoration: none;
    color: black;
}

.book-card {
    text-align: center;
    width: 200px;
    margin: 10px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 5px black;
    cursor: pointer;
    text-decoration: none;
}

.book-card:hover {
    box-shadow: 0 0 5px #007bff;
}

.book-title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
}

.book-thumbnail {
    object-fit: cover;
    margin-bottom: 10px;
}

.book-authors {
    font-size: 14px;
    margin-bottom: 5px;
}

.book-categories {
    font-size: 14px;
    margin-bottom: 10px;
}

/*LOADER*/
.loader {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.loader__spinner {
    border: 4px solid #f3f3f3;
    border-top: 4px solid #3498db;
    border-radius: 50%;
    width: 300px;
    height: 300px;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

/*BookDetailsPage*/
.book-card_page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 120px;
}

.book-details {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}

.book-details__title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
}

.book-details__author {
    font-size: 18px;
    font-style: italic;
    margin-bottom: 10px;
}

.book-details__image {
    width: 200px;
    height: 300px;
    margin-bottom: 10px;
}

.book-details__description {
    font-size: 16px;
    margin-bottom: 10px;
}


